@import "react-alice-carousel/lib/alice-carousel.css";
@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');

* {
    font-family: "Montserrat", sans-serif;
    font-weight: 500;
    font-style: normal;
    /* color: #FAF0E6; */
}
html,
body {
    height: 100vh;
    width:100vw;
    background-color: #14161a;
    max-width: 100%;
    max-height: 100%;
    overflow-x: hidden;
}
.nav{
    box-shadow: rgba(26, 75, 99, 0.15) 0px 15px 25px, rgba(0, 0, 0, 0.05) 0px 5px 10px;
}

.Carouselimg {
    background-image: url("../public/banner2.jpeg");
    width: 100vw;
    height: 50vh;
    background-repeat: no-repeat;
    background-size: cover;
}

#chartID {
    height: 0;
}

.modal,
.overlay {
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  position: fixed;
}

.overlay {
  background: rgba(49, 49, 49, 0.8);
}

.modal-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  line-height: 2.4;
  border-radius: 8px;
  min-width: 40vw;
  font-size: larger;
}

@media screen and (max-width:500px){
  .modal-content{
    width:85vw;
  }
}
@media screen and (max-width:900px){
  .modal-content{
    width:70vw;
  }
}
.active-modal{
    overflow: hidden;
}
.close {
  font-size: 1.7rem;
  position: absolute;
  right: 1rem;
  top: -1px;
  cursor: pointer;
}